<template>
  <div class="th-wrapper">
    <div class="th-image">
      <img v-bind:src="require(`@/assets/${image}`)" @click="$router.push({ path: `${link}` })" alt="test image">    
    </div>
    <div class="th-name">
      <p>{{ title }}</p>
    </div>
  </div>
  <a id="kakao-link-btn" @click="kakaoLink">
    <img
      src="https://developers.kakao.com/assets/img/about/logos/kakaolink/kakaolink_btn_medium.png"
    />
  </a>
  <v-btn  @click="copyLink" class="ma-2" color="red" dark></v-btn>
</template>

<script>

export default {
  name: 'Theme',
  props: {
    theme: String,
  },
  setup(props) {
    var image = "logo.png"
    var title = ""
    var link = ""
    if (props.theme == "lolbti") {
      image = "lol/lol.jpg"
      title = "나에게 맞는 롤 챔피언은?"
      link = "/lol"
    } else if (props.theme == "movie") {
      image = "pixar.jpg"
      title = "나에게 맞는 영화는?"
      link = "/q/movie"
    }

    return {
        image, title, link
    }
  },
  methods: {
    copyLink() {
      const t = document.createElement("textarea");
      document.body.appendChild(t);
      t.value = window.location.href;
      t.select();
      document.execCommand('copy');
      document.body.removeChild(t);
      alert("복사 되었습니다.")
    },
    kakaoLink() {
      window.Kakao.Link.sendDefault({
        objectType: 'feed',
        content: {
          title: '딸기 치즈 케익',
          description: '#케익 #딸기 #삼평동 #카페 #분위기 #소개팅',
          imageUrl:
            'http://k.kakaocdn.net/dn/Q2iNx/btqgeRgV54P/VLdBs9cvyn8BJXB3o7N8UK/kakaolink40_original.png',
          link: {
            mobileWebUrl: 'https://developers.kakao.com',
            webUrl: 'https://developers.kakao.com',
          },
        },
        social: {
          likeCount: 286,
          commentCount: 45,
          sharedCount: 845,
        },
        buttons: [
          {
            title: '웹으로 보기',
            link: {
              mobileWebUrl: 'https://developers.kakao.com',
              webUrl: 'https://developers.kakao.com',
            },
          },
          {
            title: '앱으로 보기',
            link: {
              mobileWebUrl: 'https://developers.kakao.com',
              webUrl: 'https://developers.kakao.com',
            },
          },
        ],
      })
    }
  }
}
</script>

<style>
@import url(//fonts.googleapis.com/earlyaccess/nanumpenscript.css);

.th-wrapper {
	width: 50%;
	margin: 10px auto;
	border: 1px solid #000000;
	position: relative;
}
.th-image img {
	width: 100%;
	vertical-align: middle;
}
.th-name {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 30px;
	padding: 5px 10px;
	background-color: white;
  opacity: 0.8;
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate( -50%, -50% );
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>